"use client";

import React, { useState, FormEvent } from "react";
import Image from "next/image";
import { useRouter } from "next/navigation";
import axios from "axios";
import Eye from "../../../../public/assets/Eye.png";
import EyeClosed from "../../../../public/assets/Property 1=EyeClosed.png";
import google from "../../../../public/assets/google.png";
import image1 from "../../../../public/assets/Accept tasks-amico 1.png";
import Link from "next/link";
import "../../../../src/app/globals.css";
import { toast } from "react-toastify";
import Toast from "../../../components/auth/Toast";
import headerImage from "../../../../public/assets/Logo Icon.png";
import { authService } from "../../../lib/api/services";
import { GoogleLogin } from "@react-oauth/google";

interface SignUpFormData {
  name: string;
  email: string;
  organisation: string;
  password: string;
}

export default function SignUp() {
  const router = useRouter();
  const [formData, setFormData] = useState<SignUpFormData>({
    name: "",
    email: "",
    organisation: "",
    password: "",
  });
  const [passwordValidation, setPasswordValidation] = useState({
    hasUpperCase: false,
    hasLowerCase: false,
    hasNumber: false,
    hasSpecialChar: false,
    isLongEnough: false,
  });
  const [isLoading, setIsLoading] = useState(false);
  const [visibility, setVisibility] = useState(true);
  const [passwordVisible, setPasswordVisible] = useState(false);

  const validatePassword = (password: string) => {
    setPasswordValidation({
      hasUpperCase: /[A-Z]/.test(password),
      hasLowerCase: /[a-z]/.test(password),
      hasNumber: /\d/.test(password),
      hasSpecialChar: /[!@#$%^&*(),.?":{}|<>]/.test(password),
      isLongEnough: password.length >= 12,
    });
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));

    if (name === "password") {
      validatePassword(value);
    }
  };

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();

    if (!formData.name) {
      toast.error("Name is required");
      return;
    }
    if (!formData.email) {
      toast.error("Email is required");
      return;
    }
    if (!formData.organisation) {
      toast.error("Organisation name is required");
      return;
    }
    if (!formData.password) {
      toast.error("Password is required");
      return;
    }

    const {
      isLongEnough,
      hasUpperCase,
      hasLowerCase,
      hasNumber,
      hasSpecialChar,
    } = passwordValidation;
    if (
      !isLongEnough ||
      !hasUpperCase ||
      !hasLowerCase ||
      !hasNumber ||
      !hasSpecialChar
    ) {
      toast.error("Please ensure your password meets all requirements");
      return;
    }

    setIsLoading(true);

    try {
      const response = await authService.signup({
        fullName: formData.name,
        email: formData.email,
        password: formData.password,
        organization: formData.organisation,
      });

      if (response && response.status === 201) {
        localStorage.setItem("token", response.data.token);
        toast.success("Account created successfully! Redirecting to login...");
        setTimeout(() => {
          router.push("/auth/login");
        }, 1000);
      }
    } catch (error) {
      if (axios.isAxiosError(error)) {
        if (error.response?.status === 409) {
          toast.error("An account with this email already exists");
        } else {
          toast.error("An error occurred during signup");
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleGoogleSignup = async (response: any) => {
    try {
      const { credential: idToken } = response; // Extract the Google ID token
      console.log("ID Token:", idToken);

      const res = await axios.post(
        `${process.env.NEXT_PUBLIC_API_URL}/auth/google/signup`, // Signup endpoint
        { idToken }
      );

      if (res.status === 201) {
        // 201 Created for new users
        // Store token and user data in localStorage
        localStorage.setItem("token", res.data.token);
        localStorage.setItem("userData", JSON.stringify(res.data.user));
        toast.success("Account created successfully! Redirecting...");

        // Redirect to the dashboard
        setTimeout(() => {
          window.location.href = "/main/dashboard";
        }, 1000);
      }
    } catch (error: any) {
      console.error("Error during Google Signup:", error);

      if (error.response?.status === 409) {
        // If the backend returns 409 Conflict, user already exists
        toast.error("Signup failed: User already exists. Please log in.");
      } else {
        toast.error("Signup failed. Please try again.");
      }
    }
  };

  return (
    <>
      <Toast />
      <div className="h-screen w-screen flex overflow-hidden ">
        {/* Left Section */}
        <div className="w-1/2 h-full bg-white flex items-center justify-center overflow-y-auto ">
          <div className="w-full max-w-md px-6 py-8 flex flex-col h-full scale-[0.9] origin-top-left">
            {/* Header */}
            <div className="flex items-center justify-center mb-4">
              <Image src={headerImage} alt="Logo" className="w-10 h-auto" />
              <span className="ml-2 text-lg font-sans font-bold text-[#333333]">
                <span className="font-sans text-[#333333] font-bold text-2xl">B</span>lackcofferToDos
              </span>
            </div>

            {/* Title */}
            <div className="text-center mb-4">
              <h1 className="text-xl font-bold font-sans">
                Try <span className="text-indigo-600">BlackcofferToDos</span>{" "}
                for free
              </h1>
              <p className="text-sm text-gray-600 mt-1">
                No credit card required. Cancel anytime.
              </p>
            </div>

            {/* Form */}
            <form
              onSubmit={handleSubmit}
              className="flex-grow flex flex-col space-y-3"
            >
              <div>
                <label className="block text-sm font-medium mb-1">
                  Full name
                </label>
                <input
                  type="text"
                  name="name"
                  value={formData.name}
                  onChange={handleChange}
                  className="w-full p-2.5 border border-gray-200 rounded-lg"
                  placeholder="Enter your name"
                />
              </div>

              <div>
                <label className="block text-sm font-medium mb-1">Email</label>
                <input
                  type="email"
                  name="email"
                  value={formData.email}
                  onChange={handleChange}
                  className="w-full p-2.5 border border-gray-200 rounded-lg"
                  placeholder="Enter your email"
                />
              </div>

              <div>
                <label className="block text-sm font-medium mb-1">
                  Organisation name
                </label>
                <input
                  type="text"
                  name="organisation"
                  value={formData.organisation}
                  onChange={handleChange}
                  className="w-full p-2.5 border border-gray-200 rounded-lg"
                  placeholder="Enter organisation name"
                />
              </div>

              <div>
                <label className="block text-sm font-medium mb-1">
                  Password
                </label>
                <div className="relative">
                  <input
                    type={passwordVisible ? "text" : "password"}
                    name="password"
                    value={formData.password}
                    onChange={handleChange}
                    onClick={() => setVisibility(false)}
                    className="w-full p-2.5 border border-gray-200 rounded-lg"
                    placeholder="Password (12 characters min)"
                  />
                  <button
                    type="button"
                    onClick={() => setPasswordVisible(!passwordVisible)}
                    className="absolute right-3 top-1/2 -translate-y-1/2"
                  >
                    <Image
                      src={passwordVisible ? Eye : EyeClosed}
                      alt={passwordVisible ? "Hide password" : "Show password"}
                      width={16}
                      height={16}
                    />
                  </button>
                </div>

                {/* Password Requirements */}
                {!visibility && (
                  <ul className="mt-2 space-y-0.5 text-xs">
                    <li
                      className={
                        passwordValidation.isLongEnough
                          ? "hidden"
                          : "text-red-600"
                      }
                    >
                      Must contain 12 letters
                    </li>
                    <li
                      className={
                        passwordValidation.hasUpperCase
                          ? "hidden"
                          : "text-red-600"
                      }
                    >
                      Must contain one Upper case
                    </li>
                    <li
                      className={
                        passwordValidation.hasLowerCase
                          ? "hidden"
                          : "text-red-600"
                      }
                    >
                      Must contain one Lower case
                    </li>
                    <li
                      className={
                        passwordValidation.hasNumber
                          ? "hidden"
                          : "text-red-600"
                      }
                    >
                      Must contain one Number
                    </li>
                    <li
                      className={
                        passwordValidation.hasSpecialChar
                          ? "hidden"
                          : "text-red-600"
                      }
                    >
                      Must contain one Special Character
                    </li>
                  </ul>
                )}
              </div>

              <div className="flex-grow flex flex-col justify-end space-y-3">
                <button
                  type="submit"
                  disabled={isLoading}
                  className="w-full py-2.5 bg-[#5D56BD] text-white rounded-lg hover:bg-indigo-700 transition-colors"
                >
                  {isLoading ? "Creating account..." : "Create account"}
                </button>

                <div className="relative py-2">
                  <div className="absolute inset-0 flex items-center">
                    <div className="w-full border-t border-gray-200"></div>
                  </div>
                  <div className="relative flex justify-center">
                    <span className="bg-white px-4 text-xs text-gray-500">
                      Or
                    </span>
                  </div>
                </div>
                <GoogleLogin
                  text="continue_with"
                  onSuccess={handleGoogleSignup} // Handle success
                  onError={() => console.log("Google login failed")} // Handle error
                  useOneTap
                >
                  <button
                    type="button"
                    className="w-full flex items-center justify-center p-2.5 border border-indigo-600 rounded-lg hover:bg-gray-50"
                  >
                    <Image
                      src={google}
                      alt="Google"
                      width={16}
                      height={16}
                      className="mr-2"
                    />
                    <span className="text-sm text-indigo-600">
                      Continue with Google
                    </span>
                  </button>
                </GoogleLogin>

                <div className="text-center text-xs space-y-4">
                  <p>
                    Questions? Need help?{" "}
                    <a
                      href="#"
                      className="text-[#565DBD] hover:text-indigo-800"
                    >
                      Support can help
                    </a>
                  </p>
                  <div className="mt-4 mb-8 space-y-4">
                    Already have an account?{" "}
                    <Link
                      href="/auth/login"
                      className="text-indigo-600 hover:text-indigo-800"
                    >
                      Log In
                    </Link>
                  </div>
                  <div className="w-full h-[10px]"></div>
                </div>
              </div>
            </form>
          </div>
        </div>

        {/* Right Section */}
        <div className="w-1/2 h-full bg-indigo-50 flex items-center justify-center">
          <Image
            src={image1}
            alt="Illustration"
            className="w-4/5 h-auto"
            priority={true}
          />
        </div>
      </div>
    </>
  );
}
